import React from "react";
import "./header.css";
import CTA from "./CTA";
import ME from "../../src/assets/profileImage.png";
import HeaderSocials from "./HeaderSocials";

function Header() {
  return (
    <section id="home">
      <header>
        <div className="container header_container">
          <h5>Hello I'm</h5>
          <h1>Mian Noman</h1>
          <h5 className="text-light">Fullstack Developer</h5>
          <CTA />
          <HeaderSocials />
          <div className="me">
            <img src={ME} alt="" />
          </div>
          <a href="#contact" className="scroll_down">
            Scroll Down
          </a>
        </div>
      </header>
    </section>
  );
}

export default Header;
